<template>
  <article class="content">
    <h2 class="content__title">永遠都在補課的難題</h2>
    <div class="content__text">
      <p>雲門園區內蓬勃花木中，有棵林懷民母親親手培育送給兒子的老梅樹，跟著雲門從燒毀的舊排練場到今日的新園區，林懷民每次談到此樹，總顯深情，說起已逝的父母，卻是滿滿的悔憾。這些悔憾，讓他至今每次旅行都帶著父母的照片在旅館供起來，對著他們說話，大大小小的記憶都是不捨。</p>
    </div>
    <div class="content__quote-wrapper">
      <blockquote class="content__quote">如何陪伴老去的父母是人生最大的課題，我到今天好像還在補課，可是統統來不及。</blockquote>
      <p class="content__quote-speaker">林懷民</p>
    </div>
    <div class="content__text">
      <p>林懷民回憶，有次他應邀去歐洲導演，爸媽來看首演，之後他帶他們去歐洲玩，由於媽媽堅持省錢，三人擠一間房，現在回想充滿遺憾，「我後來去演出，人家都給我住很好的套房，我為什麼就不能讓他們住套房？」</p>
    </div>
    <figure class="content__photo-wrapper">
      <img class="content__photo" src="../assets/ContentImg/mob_childhood.jpg" alt="">
      <figcaption class="content__photo-caption">圖說預留的位置&ensp;/&ensp;誰誰&ensp;攝影</figcaption>
    </figure>
    <div class="content__text">
      <p>又譬如，爸爸過世後，林懷民總在周六陪媽媽，周日由弟弟陪。有年林懷民弟弟出國，他周六在台中演完，準備周日回家，先打電話問「媽媽您好嗎」，聽到媽媽答「一個人吃飯哪有什麼好？」他聽到簡直快瘋掉，趕快衝回家。</p>
    </div>
    <div class="content__quote-wrapper">
      <blockquote class="content__quote">我們在年輕時，不知道老人家的寂寞；我到60歲知道一些事情，往往是我希望40歲知道的。</blockquote>
      <p class="content__quote-speaker">林懷民</p>
    </div>
    <div class="content__text">
      <p>林懷民2002年執導的歌劇《托斯卡》，明年將重新演出，背後也有段深刻的母子記憶。林懷民說，他第一次知道《托斯卡》中的名曲〈為了藝術為了愛〉，就是四、五歲時，媽媽放78轉老唱片給他聽，還教他認日文字「為了藝術為了愛」。</p>
      <p><br></p>
      <p>後來林懷民版《托斯卡》上演，媽媽正在住院，也興沖沖來看。上半場結束，大家想該聽醫生說的9點要回家，媽媽卻抵死不回，說要看劇中的托斯卡怎麼跳下來，但大家都不大敢不尊重醫生的意思，終說服媽媽坐輪椅回家。</p>
      <p><br></p>
      <p>「我好後悔那時為什麼不讓她看？看完了，明天再繼續生病也可以。」林懷民建議天下兒女，在過了一條線後，別太在意醫生說什麼可以吃、什麼不可以吃，「有時就讓老人家吃了吧，看是要活得很長很辛苦，還是讓他過癮一下？」</p>
    </div>
    <h2 class="content__title">退休與算命一席話有關</h2>
    <div class="content__text">
      <p>去年林懷民預告2019年退休，屆時歲數是72歲——傳言中他兩次批命，對方都指將遭大限的年齡。採訪時，我們拐著彎問林懷民預告用意，沒想到林懷民丟出直球：「是要問72歲的事對不對？我覺得，我可以不相信相命的說什麼，可是要尊敬，既然大家都說我72歲要死，我就要把它當一件事。」</p>
      <p><br></p>
      <p>林懷民說，這說法於他個人不重要，但對團而言重要。「如果有一天我突然不來上班，絕對有場大混亂」，為了舞團，他必須尊敬這說法。</p>
    </div>
    <figure class="content__photo-wrapper">
      <img class="content__photo" src="../assets/ContentImg/mob_huimin.jpg" alt="">
      <figcaption class="content__photo-caption">圖說預留的位置&ensp;/&ensp;誰誰&ensp;攝影</figcaption>
    </figure>
    <div class="content__text">
      <p>林懷民退休前最後一齣舞作《關於島嶼》裡有一段金剛經偈語：「一切有為法／如夢幻泡影／如露亦如電／應作如是觀」，反映著林懷民的生死觀。林懷民認為，夢幻泡影是人世間的必然，「你覺得你是例外嗎？沒有例外。」林懷民說得凜然，彷彿直視生死。</p>
    </div>
    <h2 class="content__title">期待台灣從「我」變成「我們」</h2>
    <div class="content__text">
      <p>林懷民能夠坦率面對自己的生死，但對於今天的台灣，他語中卻流露出憂心與期許。</p>
      <p><br></p>
      <p>林懷民說，長年以來，大家常說是哪個政黨、政客，讓台灣變成現在這個樣子，但在這同時，「我們」明明也讓這些事情發生。他認為，台灣現在不論好或不好，「都是我們的共業」。</p>
      <p><br></p>
      <p>在林懷民車禍後唯一作品、也是最後舞作《關於島嶼》中，舞者從島嶼初生蓬勃舞至相鬥幻滅，末了倏地打亮的全白舞台，猶如曹雪芹《紅樓夢》尾聲「落了片白茫茫大地真乾淨」，令人警醒美麗島的榮枯更迭，是否就如肉身易碎，只是場黃梁大夢？</p>
      <p><br></p>
      <p>但「光復仔」林懷民的救國情懷，其實跟過往一樣強悍。「台灣沒有悲觀的本錢。」林懷民說，必須去面對任何可能性，「面對了這個可能性，你就有很長的路」；其他口水對立，都可以假裝不存在，「就去做應該做的事，批評沒有用。」</p>
      <p><br></p>
      <p>林懷民感嘆，台灣一直花太多時間做各種政治分類，大家都誓不兩立，「3個人就有兩派或三派」。像雲門5歲做演繹自「唐山過台灣」歷史的《薪傳》，隔年做《廖添丁》，都源於當時社會的互動與影響，但就在同個時空，被不同人說是獨派或統派。</p>
      <p><br></p>
      <p>「來來去去的頻繁的選舉，把大家的格局做小了！」林懷民說，如果做《廖添丁》是獨，那蓋廖添丁廟的人就是台獨嗎？媽祖是從大陸來的，那去媽祖廟拜的人，就是統派嗎？從媒體開始，各界都各為其主，大家都把格局做小了。</p>
      <p><br></p>
      <p>「心胸狹小，路會愈走愈窄。」林懷民認為，台灣必須擁有更大的格局，才能真正自處。他也期待現在的年輕人，能撐出一股氣來，聚集成一大票人改變社會，「『我』當然很渺小，但當『我』變成『我們』時，就能做出一點事情來。」</p>
    </div>
    <div class="content__quote-wrapper">
      <blockquote class="content__quote">做出什麼事呢？<br>小事<br>但小事累積後就可能變成比較有規模的大事。</blockquote>
      <p class="content__quote-speaker">林懷民</p>
    </div>
  </article>
